<!-- 展示模板 -->
<template>
	<!-- <div>
    <button @click="increment">加{{this.$store.getters.count}}</button>
	</div> -->
	<div>
		<router-view></router-view>
	</div>
  
</template>
 
<script>
// 导入组件
import {mapGetters,mapActions} from 'vuex'
 

export default {
  name: 'app',
  created: function() {
    this.getUser();
  },
  methods: {
    ...mapActions([
        'getUserInfo'
    ]),
	getUser:function(){
		this.$http.jsonp(this.web_path+'scoupon/gg/selectUser.do')
		.then(function(res) {
			this.getUserInfo(res.body);
			if(this.login_flag){
				window.location.href = '#/app1';
			}else{
				window.location.href = '#/login';
			}			
		})
	}
  },
	computed: {
		...mapGetters([
			'login_flag',
			'web_path'
		])
	},
  

}
</script>




