<template>
    <div class="userCenter">
        <div class="title">个人中心</div>
        <div class="main">
            <div class="upload-box">
                <form action="https://7fanli.com:8443/Seven/al/uploadUserPic.do" method="post" enctype="multipart/form-data">
                    <div class="preview" @mouseover="showChangePic" @mouseout="hideChangePic">
                        <input class="file-field" ref="file" type="file" name="picFile" @change="fileChange" title=" ">
                        <img ref="imgWrapper" class="img" :src="user_pic" alt="">
                        <span class="tip" :style="{bottom: bottom + 'px'}"  >更换头像</span>
                    </div>
                    <div class="upload-btn">
                        <input @click="submit_img" :type="submit_btn_type" value="保存" disabled >
                    </div>
                </form>
            </div>
            <ul class="info-box">
                <li>
                    <span class="label">手机号：</span>
                    <span class="field"><input v-model="user_tel" readonly=""></span>
                </li>
                <li>
                    <span class="label">昵称：</span>
                    <span class="field">
                        <input @keyup.enter="edit_user_nick" v-model="user_nick" :readonly="!user_nick_check" :class="[user_nick_check ? 'border' : '']" maxlength="16" ref="user_nick_f">
                    </span>
                    <span v-if="!user_nick_check" @click="edit('昵称')" class="btn read"></span>
                    <span v-if="user_nick_check" @click="edit_user_nick" class="btn save">保存</span>
                </li>
                <li>
                    <span class="label">真实姓名：</span>
                    <span class="field">
                        <input @keyup.enter="edit_real_name_or_alipay_user_name('真实姓名')" v-model="real_name" :readonly="!real_name_check" :class="[real_name_check ? 'border' : '']" maxlength="16" ref="real_name_f">
                    </span>
                    <span v-if="!real_name_check" @click="edit('真实姓名')" class="btn read"></span>
                    <span v-if="real_name_check" @click="edit_real_name_or_alipay_user_name('真实姓名')" class="btn save">保存</span>
                </li>
                <li>
                    <span class="label">支付宝账号：</span>
                    <span class="field">
                        <input @keyup.enter="edit_real_name_or_alipay_user_name('支付宝账号')" v-model="alipay_user_name" :readonly="!alipay_user_name_check" :class="[alipay_user_name_check ? 'border' : '']" ref="alipay_user_name_f">
                    </span>
                    <span v-if="!alipay_user_name_check" @click="edit('支付宝账号')" class="btn read"></span>
                    <span v-if="alipay_user_name_check" @click="edit_real_name_or_alipay_user_name('支付宝账号')" class="btn save">保存</span>
                </li>
                <li>
                    <span class="label">余额：</span>
                    <span class="field">
                        <input readonly="" v-model="balance">
                    </span>
                    <span class="btn"></span>
                </li>
                <li>
                    <span class="label">创建时间：</span>
                    <span class="field">
                        <input readonly="" v-model="create_time">
                    </span>
                    <span class="btn"></span>
                </li>
                <li>
                    <span class="label">未生效金额：</span>
                    <span class="field">
                        <input readonly="" v-model="unused">
                    </span>
                    <span class="btn"></span>
                </li>
            </ul>
        </div>
        <el-dialog :visible.sync="isShowDialog" width="500px" center>
            <ul class="check-info-dialog">
                <li>
                    <div class="li-c"><el-tag>手机号：</el-tag></div>
                    <div class="li-c"><el-input v-model="user_tel" readonly></el-input></div>
                </li>
                <li>
                    <div class="li-c"><el-tag>验证码：</el-tag></div>
                    <div class="li-c"><el-input v-model="codeVal"></el-input></div>
                    <div class="li-c"><el-button type="info" plain @click="getCode" v-text="codeBtnText"></el-button></div>
                </li>
            </ul>
            <span slot="footer" class="dialog-footer">
                <el-button @click="submitCode" type="info" plain>提交</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import Jq from 'jquery'
    import { mapGetters,mapActions } from 'vuex'
    import toStorage from '../lib/storage'
    export default {
        data () {
            return {
                bottom: '-60',
                user_tel: '',
                user_nick: '',
                watch_user_nick: false,
                real_name: '',
                watch_real_name: false,
                alipay_user_name: '',
                watch_alipay_user_name: false,
                balance: '',
                create_time: '',
                unused: '',
                user_nick_check: false,
                real_name_check: false,
                alipay_user_name_check: false,
                isShowDialog: false,
                codeVal: '',
                isSendCode: true,
                codeBtnText: '获取验证码',
                codeNum: 60,
                timer: null,
                submit_btn_type: 'button'
            }
        },
        watch: {
            user_nick (val) {
                if (val == toStorage('user_nick')) {
                    this.watch_user_nick = false
                } else {
                    this.watch_user_nick = true
                }
            },
            real_name (val) {
                if (val == toStorage('real_name')) {
                    this.watch_real_name = false
                } else {
                    this.watch_real_name = true
                }
            },
            alipay_user_name (val) {
                if (val == toStorage('alipay_user_name')) {
                    this.watch_alipay_user_name = false
                } else {
                    this.watch_alipay_user_name = true
                }
            }
        },
        computed: {
            ...mapGetters([
                'web_path',
                'user_pic'
            ])
        },
        mounted: function() {
            this.$nextTick(function() {
                this.user_tel = toStorage('user_tel') || '';
                this.user_nick = toStorage('user_nick') || '';
                this.real_name = toStorage('real_name') || '';
                this.alipay_user_name = toStorage('alipay_user_name') || '';
                this.balance = toStorage('balance') || '';
                this.create_time = toStorage('create_time') || '';
                this.unused = toStorage('unused') || '';
            })
        },
        methods: {
			 ...mapActions([
				'changeReal_name',
				'changeAlipay_user_name',
				'changeUsernick'
			]),
            fileChange: function(e) {
                var _this = this;
                var file = this.$refs.file.files[0];
                if (!/image\/\w+/.test(file.type)) {
                    _this.openTips('文件必须为图片', 'warning');
                    this.$refs.file.value = '';
                    return false;
                } else if (!/\.(jpg|png|JPG|PNG)$/.test(this.$refs.file.value)) {
                    _this.openTips('请选择类型为jpg或png的图片', 'warning');
                    this.$refs.file.value = '';
                    return false;
                } else if ((file.size/1024).toFixed(2) > 1024) {
                    _this.openTips('图片文件过大', 'warning');
                    this.$refs.file.value = '';
                    return false;
                } else {
                    var fileReader = new FileReader();
                    fileReader.readAsDataURL(e.target.files[0]);
                    fileReader.onload = function() {
                        // _this.user_pic = this.result;
                        _this.$refs.imgWrapper.setAttribute('src', this.result);
                    }
                }
            },
            submit_img: function() {
				if(true) return;
                if (!this.$refs.file.value) {
                    this.openTips('请选择图片', 'warning');
                } else {
                    this.submit_btn_type = 'submit';
                }
            },
            showChangePic: function() {
                this.bottom = 0;
            },
            hideChangePic: function() {
                this.bottom = '-60';
            },
            edit: function(val) {
                switch (val) {
                    case '昵称':
                    this.user_nick_check = !this.user_nick_check;
                    this.$refs.user_nick_f.focus();
                    break;
                    case '真实姓名':
                    this.alipay_user_name_check = false;
                    this.real_name_check = !this.real_name_check;
                    this.$refs.real_name_f.focus();
                    break;
                    case '支付宝账号':
                    this.real_name_check = false;
                    this.alipay_user_name_check = !this.alipay_user_name_check;
                    this.$refs.alipay_user_name_f.focus();
                    break;
                }
            },
            edit_user_nick: function() {
                if (this.watch_user_nick) {
                var _this = this;
                Jq.ajax({
                    url: this.web_path+'scoupon/user/upUsernick.do',
                    type: 'GET',
                    data: {
                        'user_nick': encodeURI(this.user_nick)
                    },
                    dataType: 'jsonp',
                    success: function(res) {
                        if (res.nickCount == 1) { 
                            _this.openTips('修改成功', 'success');
                            _this.user_nick_check = false;
                            _this.changeUsernick(_this.user_nick);
                        }
                    }
                })
                } else {
                    this.user_nick_check = false;
                }
            },
            edit_real_name_or_alipay_user_name: function(type) {
                switch (type) {
                    case '真实姓名':
                    if (this.watch_real_name) {
						if(this.codeVal!="" && this.codeVal!=null){
							this.changeUser();
						}else{
							this.isShowDialog = true
						}
                    } else {
                        this.real_name_check = false;
                    }
                    break;
                    case '支付宝账号':
                    if (this.watch_alipay_user_name) {
						if(this.codeVal!="" && this.codeVal!=null){
							this.changeUser();
						}else{
							this.isShowDialog = true
						}
                    } else {
                        this.alipay_user_name_check = false;
                    }
                    break;
                }
            },
            getCode: function() {
                if (this.isSendCode) {
                    this.codeBtnText = '正在发送...';
                    var _this = this;
                    Jq.ajax({
                        url: this.web_path+'scoupon/user/alipayInfoSMV.do?tel='+_this.user_tel+'&t='+new Date().getTime(),
                        type: 'GET',
                        dataType: 'jsonp',
                        success: function(res) {
                            _this.openTips('验证码已发送至您的手机，请查收~', 'success');
                            _this.codeTimer();
                        }
                    })
                }
            },
            codeTimer: function() {
                var _this = this;
                this.isSendCode = false;
                this.timer = setInterval(function() {
                    _this.codeNum--;
                    if (_this.codeNum > 0) {
                        _this.codeBtnText = _this.codeNum + '秒后可重新获取'
                    } else {
                        clearInterval(_this.timer);
                        _this.codeBtnText = '重新发送验证码';
                        _this.codeNum = 60;
                        _this.isSendCode = true;
                    }
                }, 1000)
            },
            submitCode: function() {
                var _this = this;
                if (this.codeVal == '') {
                    this.openTips('请输入验证码~', 'warning');
                } else {
					_this.changeUser()
                }
            },
            changeUser: function() {
                var _this = this;
                Jq.ajax({
                    url: this.web_path+'scoupon/user/alipayInfoSave.do',
                    type: 'GET',
                    data: {
                        'real_name': encodeURI(_this.real_name),
                        'alipay_user_name': _this.alipay_user_name,
                        'identCode': _this.codeVal
                    },
                    dataType: 'jsonp',
                    success: function(res) {
                        if (res.msg == 'success') {
                            _this.isShowDialog = false;
                            _this.openTips('修改成功', 'success');
                            _this.changeReal_name(_this.real_name);
							_this.changeAlipay_user_name(_this.alipay_user_name);
                            _this.real_name_check = false;
                            _this.alipay_user_name_check = false;
                            clearInterval(_this.timer);
                            _this.codeBtnText = '获取验证码';
                            _this.codeNum = 60;
                            _this.isSendCode = true;
                        }else{
							_this.openTips('更新失败', 'warning');
						}
                    }
                })
            },
            openTips: function(text, type) {
                this.$message({
                    showClose: true,
                    message: text,
                    type: type
                });
            }
        }
    }
</script>

<style lang="less" src="../style/userCenter.less"></style>


