<!-- 展示模板 -->
<template>
   <div class="loginBoxCenter" style="height: 250px;">
		<p>&nbsp;<span   class="errSpan">{{errMsg}}</span></p>
		<p><label for="username">用户名：</label>
		<input type="text" id="text" name="text" class="loginInput" autofocus="autofocus" required="required" autocomplete="off" placeholder="请输入手机号" v-model="user_tel" /></p>
		<p><label for="identCodeBtn">验证码：</label>
		<input type="text" id="identCode" name="identCode" class="identCodeInput" required="required"  v-model="identCode" /> 
		<button id='identCodeBtn1' class="identCodeBtn"  v-on:click="getIdentCode" >获取验证码</button></p>
		<p><label for="password">密码&nbsp;&nbsp;&nbsp;：</label>
		<input type="password" id="password" name="password" class="loginInput" required="required" placeholder="请输入新密码" v-model="password" @keyup.enter="upPasswd" /></p>
		<p><button class="loginBtn" v-on:click="upPasswd" >修改密码</button></p>
	</div>
</template>
 
<script>
import {mapState,mapActions} from 'vuex'

// 导入组件 
export default {
		  name: 'loginpw',
		  data() {
					return {
						user_tel: '',
						identCode: '',
						countdown: 60,
						errMsg: '',
						password: ''
					}
				},
		methods: {
			upPasswd: function() {
                this.$http.jsonp(this.web_path+'scoupon/gg/updatePW.do?user_tel='+this.user_tel+'&identCode='+this.identCode+'&password='+this.password)
                .then(function(res) {
					var count = res.body.count;
					if(count==1 || count=='1') {
						window.location.href = '#/login';
					}
					else {
						this.errMsg = "修改密码失败";
					}
                })
			},
			getIdentCode: function() {
                this.$http.jsonp(this.web_path+'scoupon/gg/upPasswdSMV.do?tel='+this.user_tel)
                .then(function(res) {
					var flag = res.body.msgFlag;
					if(flag=='true' || flag==true) {
					}
					else {
						this.errMsg = "验证码发送失败";
					}
					this.afterSendCOde();
                })
            },
		afterSendCOde:function () { 
			var fun = this.afterSendCOde;
			var obj = document.getElementById('identCodeBtn1');
			if (this.countdown == 0) { 
				obj.removeAttribute("disabled");
				obj.style.background =  "" ;				
				obj.innerHTML ="获取验证码"; 
				this.countdown = 60; 
				return;
			} else { 
				obj.setAttribute("disabled", true); 
				obj.style.background =  "#8e8989" ; 
				obj.innerHTML ="重新发送" + this.countdown ; 
				this.countdown--; 
			} 
		setTimeout(function() { fun() } , 1000 ) 
		}
		
	},
	computed:
		mapState(['web_path'])
}
</script>
<style lang="less" src="../style/login.less"></style>




