export default {
    formatCreateTime: (createTime) => {
        return new Date(createTime).getFullYear()+'-'+((new Date(createTime).getMonth()-(-1)) >= 10? (new Date(createTime).getMonth()-(-1)) : '0'+(new Date(createTime).getMonth()-(-1)))+'-'+(new Date(createTime).getDate() >= 10 ? new Date(createTime).getDate() : '0'+new Date(createTime).getDate())+' '+(new Date(createTime).getHours() >= 10 ? new Date(createTime).getHours() : '0'+new Date(createTime).getHours())+':'+(new Date(createTime).getMinutes() >= 10 ? new Date(createTime).getMinutes() : '0'+new Date(createTime).getMinutes())+':'+(new Date(createTime).getSeconds() >= 10 ? new Date(createTime).getSeconds() : '0'+new Date(createTime).getSeconds())
    },
    formatEarnTime: (earningTime) => {
        if (earningTime) {
            return new Date(earningTime).getFullYear()+'-'+((new Date(earningTime).getMonth()-(-1)) >= 10? (new Date(earningTime).getMonth()-(-1)) : '0'+(new Date(earningTime).getMonth()-(-1)))+'-'+(new Date(earningTime).getDate() >= 10 ? new Date(earningTime).getDate() : '0'+new Date(earningTime).getDate())+' '+(new Date(earningTime).getHours() >= 10 ? new Date(earningTime).getHours() : '0'+new Date(earningTime).getHours())+':'+(new Date(earningTime).getMinutes() >= 10 ? new Date(earningTime).getMinutes() : '0'+new Date(earningTime).getMinutes())+':'+(new Date(earningTime).getSeconds() >= 10 ? new Date(earningTime).getSeconds() : '0'+new Date(earningTime).getSeconds());
        }
        return '未结算';
    }
}


