<style>
    .searchRes-content{height: 100%;}
    .searchRes-content .el-table th{background: #616876;color: #ffffff;}
    .searchRes-content .el-pagination{text-align: center;padding: 10px 0;background: #ffffff;}
    .searchRes-content .el-pagination.is-background .el-pager li.active{background: #616876;}
    .el-input.is-active .el-input__inner, .el-input__inner:focus{border-color: #dcdfe6;}
</style>

<template>
    <div class="searchRes-content" ref="wrapper" v-loading="loading" element-loading-background="rgba(255, 255, 255, 0.2)">
        <el-input placeholder="输入您想要查询的内容，按下Enter键搜索" prefix-icon="el-icon-search" v-model="searchText" @keyup.enter.native="searchList"></el-input>
        <el-table :data="list" :height="tableHeight" border style="width: 100%">
            <el-table-column prop="createTime" label="创建时间" :formatter="formatter" width="180"></el-table-column>
            <el-table-column prop="taobaoTradeParentId" label="订单ID" width="180"></el-table-column>
            <el-table-column prop="auctionTitle" label="商品" width="400"></el-table-column>
            <el-table-column prop="auctionNum" label="商品数量"></el-table-column>
            <el-table-column prop="exShopTitle" label="所属店铺"></el-table-column>
            <el-table-column prop="feeString" label="返购物金"></el-table-column>
            <el-table-column prop="payStatus" label="订单状态">
                <template slot-scope="scope">
                    <span v-if="scope.row.payStatus == 12">订单付款</span>
                    <span v-else-if="scope.row.payStatus == 13" style="color: #f00">订单失效</span>
                    <span v-else style="color: #76a351">订单结算</span>
               </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import Filter from '../lib/filter'
    export default {
        data() {
            return {
                tableHeight: null,
                list: [],
                searchText: this.$route.query.searchText,
                loading: false
            }
        },
        mounted: function() {
            this.$nextTick(function() {
                this.tableHeight = document.body.clientHeight - 200;
                this.getList();
            })
        },
        methods: {
            searchList: function() {
                this.getList();
            },
            getList: function(currentPage) {
                this.loading = true;
                var _this = this;
                this.$http.jsonp('https://7fanli.com:8443/Seven/al/getTradeList.do?pageSize=100000&pageNo=1&param='+_this.searchText+'&startDate=2010-07-26+12%3A00%3A00&endDate=2020-07-26+12%3A00%3A00&_=1514276416367')
                .then(function(res) {
                    _this.list = res.body.list;
                    _this.total = res.body.count;
                    _this.loading = false;
                })
            },
            formatter: function(row) {
                return Filter.formatCreateTime(row.createTime);
            }
        }
    }
</script>


