<template>
    <div class="detail" ref="wrapper" v-loading="loading" element-loading-background="rgba(255, 255, 255, 0.2)">
		<div>
			<el-input placeholder="输入您想要查询的内容，按下Enter键搜索" prefix-icon="el-icon-search" style="width:50%" v-model="searchText" @keyup.enter.native="searchList"></el-input>
			<select style="height: 40px;border: 1px solid #dcdfe6;    border-radius: 4px;" v-model="payStatus" @change="getList">
				<option value="">全部订单</option>
				<option value="3">订单结算</option>
				<option value="12">订单付款</option>
				<option value="13">订单失效</option>
			</select>
		</div>
        <el-table :data="list" :height="tableHeight" stripe highlight-current-row border style="width: 100%">
            <el-table-column prop="createTime" label="创建时间" :formatter="formatter" width="180"></el-table-column>
            <el-table-column prop="earningTime" label="确认收货时间" :formatter="formatterEaringTime" width="180"></el-table-column>
            <el-table-column prop="taobaoTradeParentId" label="订单ID" width="180"></el-table-column>
            <el-table-column prop="auctionTitle" label="商品" width="400"></el-table-column>
            <el-table-column prop="auctionNum" label="商品数量"></el-table-column>
            <el-table-column prop="exShopTitle" label="所属店铺"></el-table-column>
            <el-table-column prop="feeString" label="返购物金"></el-table-column>
            <el-table-column prop="payStatus" label="订单状态">
                <template slot-scope="scope">
                    <span v-if="scope.row.payStatus == 12">订单付款</span>
                    <span v-else-if="scope.row.payStatus == 13" style="color: #f00">订单失效</span>
                    <span v-else style="color: #76a351">订单结算</span>
               </template>
            </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next" :page-size="pageSize" :total="total" @current-change="switchPage"></el-pagination>
    </div>
</template>

<script>
    import Filter from '../lib/filter'
    import { mapGetters,mapActions } from 'vuex'
    export default {
        data() {
            return {
                tableHeight: null,
                list: [],
                searchText: '',
                payStatus: null,
                total: 0,
                pageSize: 15,
                loading: false
            }
        },
        created: function() {
            this.changeIndex(0);
        },
        mounted: function() {
            this.$nextTick(function() {
                this.tableHeight = document.body.clientHeight - 250;
                this.getList(1);
            })
        },
		computed: {
			...mapGetters([
				'web_path'
			])
		},
        methods: {
            ...mapActions([
                'changeIndex'
            ]),
            searchList: function() {
                this.$router.push({path: '/searchRes', query: {searchText: this.searchText}});
            },
            getList: function(currentPage) {
                this.loading = true;
                var _this = this;
                this.$http.jsonp(this.web_path+'scoupon/user/getTradeList.do?pageSize='+this.pageSize+'&pageNo='+currentPage+'&payStatus='+this.payStatus+'&startDate=2010-07-26+12%3A00%3A00&endDate=2020-07-26+12%3A00%3A00&_=1514276416367')
                .then(function(res) {
                    if (res.flag == 'false') {
                        //window.location.href = 'fanliht/login.html';
                    } else {
                        _this.list = res.body.list;
                        _this.total = res.body.count;
                        _this.loading = false;
                    }
                })
            },
            switchPage: function(currentPage) {
                this.getList(currentPage);
            },
            formatter: function(row) {
                return Filter.formatCreateTime(row.createTime);
            },
            formatterEaringTime: function(row) {
                if (row.earningTime) {
                    return Filter.formatCreateTime(row.earningTime)
                }
                return '未结算'
            }
        }
    }
</script>

<style lang="less" src="../style/orderDetail.less"></style>


