<!-- 展示模板 -->
<template>
   <div class="loginBoxCenter" >
		<p>&nbsp;<span   class="errSpan">{{errMsg}}</span></p>
		<p><label for="username">用户名：</label>
		<input type="text" id="text" name="text" class="loginInput" autofocus="autofocus" required="required" autocomplete="off" placeholder="请输入手机号" v-model="user_tel" /></p>
		<p><label for="identCodeBtn">验证码：</label>
		<input type="text" id="identCode" name="identCode" class="identCodeInput" required="required"  v-model="identCode" @keyup.enter="SMVLogin" /> 
		<button id='identCodeBtn1' class="identCodeBtn"  v-on:click="getIdentCode" >获取验证码</button></p>
		<p><button class="loginBtn" v-on:click="SMVLogin" >登陆</button></p>
	</div>
</template>
 
<script>
import {mapState,mapActions} from 'vuex'

// 导入组件 
export default {
		  name: 'codelogin',
		  data() {
					return {
						user_tel: '',
						identCode: '',
						countdown: 60,
						errMsg: ''
					}
				},
		methods: {
			SMVLogin: function() {
                this.$http.jsonp(this.web_path+'scoupon/gg/SMVLogin.do?user_tel='+this.user_tel+'&identCode='+this.identCode)
                .then(function(res) {
					var flag = res.body.flag;
					if(flag=='true' || flag==true) {
						window.location.href = '#/app1';
					}
					else {
						this.errMsg = "登陆失败";
					}
                })
			},
			getIdentCode: function() {
                this.$http.jsonp(this.web_path+'scoupon/gg/loginSMV.do?tel='+this.user_tel)
                .then(function(res) {
					var flag = res.body.msgFlag;
					if(flag=='true' || flag==true) {
					}
					else {
						this.errMsg = "验证码发送失败";
					}
					this.afterSendCOde();
                })
            },
		afterSendCOde:function () { 
			var fun = this.afterSendCOde;
			var obj = document.getElementById('identCodeBtn1');
			if (this.countdown == 0) { 
				obj.removeAttribute("disabled");
				obj.style.background =  "" ;				
				obj.innerHTML ="获取验证码"; 
				this.countdown = 60; 
				return;
			} else { 
				obj.setAttribute("disabled", true); 
				obj.style.background =  "#8e8989" ; 
				obj.innerHTML ="重新发送" + this.countdown ; 
				this.countdown--; 
			} 
			setTimeout(function() { fun() } , 1000 ) 
		}
	},
	computed:
		mapState(['web_path'])
}
</script>
<style lang="less" src="../style/login.less"></style>




