import Vue from 'vue'
import Router from 'vue-router'
import OrderDetail from '@/components/orderDetail'
import SearchResult from '@/components/searchResult'
import DrawMoney from '@/components/drawMoney'
import DrawDetail from '@/components/drawDetail'
import ReturnDetail from '@/components/returnDetail'
import UserCenter from '@/components/userCenter'
import Loginlogin from '@/components/loginlogin'
import Loginpw from '@/components/loginpw'
import Loginregist from '@/components/loginregist'
import Codelogin from '@/components/codelogin'
import Login from '@/components/login'
import App1 from '@/App1'

Vue.use(Router)

export default new Router({
  routes: [
	{
	  path: 'searchResult',
	  name: '搜索结果列表',
	  component: SearchResult
	},
	/*{
	  path: '/',
	  redirect: '/login'
	},*/
	{
      path: '/login',
      name: '登陆页面集合',
      component: Login,
      children: [
		{
		  path: '',
		  redirect: 'loginlogin'
		},
		{
		  path: 'loginlogin',
		  name: '登陆页面',
		  component: Loginlogin
		},
		{
		  path: 'loginpw',
		  name: '忘记密码',
		  component: Loginpw
		},
		{
		  path: 'codelogin',
		  name: '验证码登陆',
		  component: Codelogin
		},
		{
		  path: 'loginregist',
		  name: '用户注册',
		  component: Loginregist
		}
		
      ]	  
    },
	{
      path: '/app1',
      name: '功能集合页面',
      component: App1,
      children: [
		{
		  path: '',
		  redirect: 'orderDetail'
		},
		{
		  path: 'orderDetail',
		  name: '订单明细',
		  component: OrderDetail,
		  children: [
			{
			  path: 'searchRes',
			  redirect: '/SearchResult'
			}
		  ]
		},
		{
		  path: 'drawMoney',
		  name: '提现',
		  component: DrawMoney
		},
		{
		  path: 'drawDetail',
		  name: '提现明细',
		  component: DrawDetail
		},
		{
		  path: 'returnDetail',
		  name: '返现明细',
		  component: ReturnDetail
		},
		{
		  path: 'userCenter',
		  name: '个人中心',
		  component: UserCenter
		}
      ]	  
    },
  ]
})



