<template>
    <div class="drawDetail-content" ref="wrapper" v-loading="loading" element-loading-background="rgba(255, 255, 255, 0.2)">
        <el-table :data="list" highlight-current-row :height="tableHeight" border style="width: 100%">
            <el-table-column prop="time" label="时间" :formatter="formatter"></el-table-column>
            <el-table-column prop="alipay_user_name" label="支付宝账户"></el-table-column>
            <el-table-column prop="user_nick" label="用户昵称"></el-table-column>
            <el-table-column prop="money" label="金额"></el-table-column>
            <el-table-column prop="type" label="状态">
                <template slot-scope="scope">
                    <span v-if="scope.row.type == 1">已结算，可提现</span>
                    <span v-else-if="scope.row.type == 2">分享提成</span>
                    <span v-else-if="scope.row.type == 3">提现中</span>
                    <span v-else-if="scope.row.type == 4" style="color: #76a351">提现成功</span>
                    <span v-else style="color: #f00">提现失败</span>
               </template>
            </el-table-column>
            <el-table-column prop="reject" label="备注"></el-table-column>
        </el-table>
    </div>
</template>

<script>
    import { mapGetters,mapActions } from 'vuex'
    import Filter from '../lib/filter'
    import toStorage from '../lib/storage'
    export default {
        data() {
            return {
                tableHeight: null,
                list: [],
                loading: false,
                user_id: ''
            }
        },
        created: function() {
            this.changeIndex(2);
            this.user_id = toStorage('user_id') || '';
        },
        mounted: function() {
            this.$nextTick(function() {
                this.checked = true;
                this.tableHeight = document.body.clientHeight - 160;
                this.getList();
            })
        },
		computed: {
			...mapGetters([
				'web_path'
			])
		},
        methods: {
            ...mapActions([
                'changeIndex'
            ]),
            getList: function() {
                this.loading = true;
                var _this = this;
                this.$http.jsonp(this.web_path+'scoupon/user/tradeList.do?&_=1514280622492')
                .then(function(res) {
                    _this.list = res.body;
                    _this.loading = false;
                })
            },
            formatter: function(row) {
                return Filter.formatCreateTime(row.time);
            }
        }
    }
</script>

<style lang="less" src="../style/drawDetail.less"></style>


