<template>
    <div class="drawMoney-content">
        <div class="wxtip">每月21号结算上月已确认收货的订单佣金。因订单量大，结算时间长，建议22号再进行提现操作。</div>
        <div class="el-row-box">
            <el-row :gutter="20">
                <el-col :span="6">
                    <div class="grid-content bg-purple">
                        <ul class="block">
                            <li><img src="../assets/all.png" alt=""></li>
                            <li>
                                <div>
                                    <span>累计总佣金</span>
                                    <el-tooltip class="item" effect="light" content="使用7返利购物期间内，已结算订单，获得的全部佣金（不含退款订单和维权订单）。" placement="top-start">
                                        <el-button><img src="../assets/wh.png" alt=""></el-button>
                                    </el-tooltip>
                                </div>
                                <div><span class="nums" v-text="total_amt"></span>元</div>
                            </li>
                        </ul>
                    </div>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple">
                        <ul class="block">
                            <li><img src="../assets/candraw.png" alt=""></li>
                            <li>
                                <div>
                                    <span>可提现佣金</span>
                                    <el-tooltip class="item" effect="light" content="上一个自然月，已确认收货的订单，每月22日结算后，未提现的金额。" placement="top-start">
                                        <el-button><img src="../assets/wh.png" alt=""></el-button>
                                    </el-tooltip>
                                </div>
                                <div><span class="nums" v-text="balance"></span>元</div>
                            </li>
                        </ul>
                    </div>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple">
                        <ul class="block">
                            <li><img src="../assets/drawall.png" alt=""></li>
                            <li>
                                <div>
                                    <span>提现总金额</span>
                                    <el-tooltip class="item" effect="light" content="使用7返利购物期间内，已提现的总金额。" placement="top-end">
                                        <el-button><img src="../assets/wh.png" alt=""></el-button>
                                    </el-tooltip>
                                </div>
                                <div><span class="nums" v-text="draw_money"></span>元</div>
                            </li>
                        </ul>
                    </div>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple">
                        <ul class="block">
                            <li><img src="../assets/drawing.png" alt=""></li>
                            <li>
                                <div>
                                    <span>正在提现</span>
                                    <el-tooltip class="item" effect="light" content="已申请提现，未到账金额（申请后24小时内到账，遇到周六日自动顺延至下一工作日）" placement="top-end">
                                        <el-button><img src="../assets/wh.png" alt=""></el-button>
                                    </el-tooltip>
                                </div>
                                <div><span class="nums" v-text="total_task_money"></span>元</div>
                            </li>
                        </ul>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div class="draw-do">
            <ul>
                <li class="d-text">每月22号，提现”上个月已确认收货“的订单返利。</li>
                <li class="d-text">无余额，请核对订单确认收货时间。</li>
                <li class="d-input"><el-input v-model="drawNum" placeholder="请输入您要提现的金额"></el-input></li>
                <li class="d-tips"><el-alert v-show="isSHowAlert" :title="title" :type="type"></el-alert></li>
                <li class="d-btn"><el-button @click="drawMoney" type="success" plain>提 现</el-button></li>
            </ul>
        </div>
        <el-dialog title="检测到您当前的账户信息为" :visible.sync="isShowDialog" width="25%" center> 
            <div>
                <el-tag type="info">姓名：</el-tag>
                <el-tag class="label" type="info" v-text="real_name"></el-tag>
            </div>
            <div>
                <el-tag type="info">支付宝账户：</el-tag>
                <el-tag class="label" type="info" v-text="alipay_user_name"></el-tag>
            </div>
            <div><el-tag type="info">是否提现到此账户？</el-tag></div>
            <span slot="footer" class="dialog-footer">
                <el-button type="success" plain @click="toUserCenter">否，更换账户</el-button>
                <el-button type="success" @click="toDraw">是，立即提现</el-button>
            </span>
        </el-dialog>

        <!--<div class="layer">
            <el-dialog title="亲爱的7返利用户：" :visible.sync="daysDialog" width="30%">
                <p>您好！</p>
                <p class="text">因2月22日恰逢春节假期，故1月已确认收货的订单，结算时间延后至2月24日，因订单量大，建议大家在2月25日关注账户余额及操作提现动作。同时提前祝7返利用户新春快乐！</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="success" plain @click="daysDialog = false">关闭</el-button>
                    <el-button type="success" plain @click="IKnow">知道了</el-button>
                </span>
            </el-dialog>
        </div>-->
    </div>
</template>

<script>
    import Jq from 'jquery'
    import { mapGetters,mapActions } from 'vuex'
    import toStorage from '../lib/storage'
    export default {
        data () {
            return {
                total_amt: 0,
                balance: 0,
                draw_money: 0,
                total_task_money: 0,
                drawNum: '',
                title: '',
                type: '',
                isSHowAlert: false,
                isShowDialog: false,
                alipay_user_name: '',
                real_name: '',
                // daysDialog: false
            }
        },
        created: function() {
            this.changeIndex(1)
        },
        mounted: function() {
            this.$nextTick(function() {
                this.alipay_user_name = toStorage('alipay_user_name');
                this.real_name = toStorage('real_name');
                this.getAmtInfo();
                // if (!toStorage('iknow')) {
                //     this.daysDialog = true
                // }
            })
        },
		computed: {
			...mapGetters([
				'web_path'
			])
		},
        methods: {
            ...mapActions([
                'changeIndex'
            ]),
            getAmtInfo: function() {
                var _this = this;
                Jq.ajax({
                    url: this.web_path+'scoupon/user/getAmtInfo.do',
                    type: 'GET',
                    dataType: 'jsonp',
                    success: function(res) {
                        if (res.flag == 'false') {
                        } else {
                            _this.total_amt = res.total_amt.toFixed(2);
                            _this.balance = res.balance.toFixed(2);
                            _this.draw_money = res.draw_money.toFixed(2);
                            _this.total_task_money = res.total_task_money.toFixed(2);
                        }
                    }
                })
            },
            drawMoney: function() {
                if (!this.alipay_user_name || !this.real_name) {
                    this.alertTip('亲，请先到个人中心完善您的支付宝和姓名信息', 'info')
                } else if (!this.drawNum || Number(this.drawNum) <= 0) {
                    this.alertTip('请输入您要提现的金额', 'info')
                } else if (Number(this.drawNum) > Number(this.balance)) {
                    this.alertTip('您目前一共可提现的佣金为'+this.balance+'元', 'info')
                } else if (!this.checkNum()) {
                    this.alertTip('您目前一共可提现的佣金为'+this.balance+'元', 'info')
                } else {
                    this.isShowDialog = true;
                }
            },
            toUserCenter: function() {
                this.$router.push('userCenter')
            },
            toDraw: function() {
                this.isShowDialog = false;
                var _this = this;
                Jq.ajax({
                    url: this.web_path+'scoupon/user/payTastSave.do',
                    type: 'GET',
                    data: {
                        'money': _this.drawNum
                    },
                    dataType: 'jsonp',
                    success: function(res) {
                        if(res.count == 1){
                            _this.alertTip('提现成功，预计一个工作日内到帐，请注意查收', 'success');
                        }else if (res.count == 0) {
                            _this.alertTip(res.msg, 'info');
                        }
                    }
                })
            },
            alertTip: function(title, type) {
                this.title = title;
                this.type = type;
                this.isSHowAlert = true;
            },
            checkNum: function() {
                if(!(/^\d+(\.\d+)?$/.test(this.drawNum))){
                    return false;
                }
                return true;
            }
        }
    }
</script>

<style lang="less" src="../style/drawMoney.less"></style>


