<template>
    <div class="returnDetail" ref="wrapper" v-loading="loading" element-loading-background="rgba(255, 255, 255, 0.2)">
        <el-table :data="list" :height="tableHeight" highlight-current-row border style="width: 100%">
            <el-table-column prop="createTime" label="创建时间" :formatter="formatter" width="180"></el-table-column>
            <el-table-column prop="earningTime" label="确认收货时间" :formatter="formatterEarningTime" :render-header="earningTimeLabel" width="180"></el-table-column>
            <el-table-column prop="auctionTitle" label="商品信息" width="400"></el-table-column>
            <el-table-column prop="feeString" label="预估返现金额" :render-header="feeStringLabel"></el-table-column>
            <el-table-column prop="finalfee" label="实际返现金额" :render-header="finalfeeLabel"></el-table-column>
            <el-table-column prop="service_fee" label="7返利服务费" :render-header="service_feeLabel"></el-table-column>
            <el-table-column prop="tax_fee" label="交税" :render-header="tax_feeLabel"></el-table-column>
            <el-table-column prop="tb_service_fee" label="阿里妈妈服务费" :render-header="tb_service_feeLabel"></el-table-column>
        </el-table>
        <div ref="pagination" class="paging">
			<a @click="pageTurning(-1)" class="jump" href="javascript:void(0)">上一页</a>
			<a class="jump" href="javascript:void(0)" v-text="pageNo"></a>
			<a @click="pageTurning(1)" class="jump" href="javascript:void(0)">下一页</a>
			<a class="jumpTo" href="javascript:void(0)">跳转到</a>
			<input @keyup.enter="jumpTo" class="jump page-num" v-model="jumpText">
			<a @click="jumpTo" class="jump" href="javascript:void(0)">GO</a>
		</div>
    </div>
</template>

<script>
    import { mapActions } from 'vuex'
    import Jq from 'jquery'
    import Filter from '../lib/filter'
    import toStorage from '../lib/storage'
    export default {
        data() {
            return {
                tableHeight: null,
                list: [],
                loading: false,
                user_id: '',
                pageNo: 1,
                pageSize: 15,
                jumpText: '',
                isNext: true
            }
        },
        watch: {
			pageNo: function() {
				this.getList();
			}
		},
        created: function() {
            this.changeIndex(3);
            this.user_id = toStorage('user_id') || '';
        },
        mounted: function() {
            this.$nextTick(function() {
                this.tableHeight = document.body.clientHeight - this.$refs.pagination.getBoundingClientRect().height - 160;
                this.getList();
            })
        },
        methods: {
            ...mapActions([
                'changeIndex'
            ]),
            pageTurning: function(val) {
				switch (val) {
					case -1:
					this.pageNo--;
					if (this.pageNo < 1) {
						this.pageNo = 1
					}
					break;
					case 1:
                    if (this.isNext) {
                        this.pageNo++;
                    }
					break;
				}
			},
			jumpTo: function() {
				this.pageNo = this.jumpText;
			},
            getList: function() {
                this.loading = true;
                this.list = [];
                var _this = this;
                Jq.ajax({
                    url: 'https://7fanli.com:8443/Seven/fanli/incomeDetail.do',
                    type: 'GET',
                    data: {
                        'user_id': _this.user_id,
                        'pageNo': _this.pageNo,
                        'pageSize': _this.pageSize
                    },
                    dataType: 'jsonp',
                    success: function(res) {
                        _this.list = res;
                        _this.loading = false;
                        if (res.length < _this.pageSize) {
                            _this.isNext = false;
                        } else {
                            _this.isNext = true;
                        }
                    }
                })
            },
            formatter: function(row) {
                return Filter.formatCreateTime(row.createTime);
            },
            formatterEarningTime: function(row) {
                return Filter.formatEarnTime(row.earningTime);
            },
            earningTimeLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="每单返利时间为该单结算时间的下月20号左右。例如：结算时间为09月01号，那么返利时间大概为10月20号。" placement="top-start">
                        <el-button>确认收货时间</el-button>
                    </el-tooltip>
                )
            },
            feeStringLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="以买家付款金额为基数，预估该笔订单可能获得的收入。因买家退款等原因，可能与订单最终收入不一致，最终收入以每月实际结算金额为准。" placement="top-start">
                        <el-button>预估返现金额</el-button>
                    </el-tooltip>
                )
            },
            finalfeeLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="以买家确认收货时的付款金额为基数， 每月20日结算实际返现金额。" placement="top-start">
                        <el-button>实际返现金额</el-button>
                    </el-tooltip>
                )
            },
            service_feeLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="用户使用7返利平台赚取佣金后，7返利平台向用户收取的服务费。" placement="top-start">
                        <el-button>7返利服务费</el-button>
                    </el-tooltip>
                )
            },
            tax_feeLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="按国家税务相关部门规定预先代扣的税金。" placement="top-end">
                        <el-button>交税</el-button>
                    </el-tooltip>
                )
            },
            tb_service_feeLabel(h) {
                return (
                    <el-tooltip class="item" effect="light" content="阿里妈妈平台向用户收取10%的服务费，于每月20日结算时收取。" placement="top-end">
                        <el-button>阿里妈妈服务费</el-button>
                    </el-tooltip>
                )
            }
        }
    }
</script>

<style lang="less" src="../style/returnDetail.less"></style>


