<!-- 展示模板 -->
<template>
	<div class="loginBoxCenter">
		<p>&nbsp;<span  v-if="errFlag" class="errSpan">用户名密码不匹配</span></p>
		<p><label for="username">用户名：</label>
		<!--autofocus 规定当页面加载时按钮应当自动地获得焦点。 -->
		<!-- placeholder提供可描述输入字段预期值的提示信息-->
		<input type="text" id="text" name="text" class="loginInput" autofocus="autofocus" required="required" autocomplete="off" placeholder="请输入手机号" v-model="user_tel" /></p>
		<!-- required 规定必需在提交之前填写输入字段-->
		<p><label for="password">密码&nbsp;&nbsp;&nbsp;：</label>
		<input type="password" id="password" name="password" class="loginInput" required="required" placeholder="请输入密码" v-model="password" @keyup.enter="login" /></p>
		<p><button class="loginBtn" v-on:click="login">登录</button></p>
		<span v-if="false" >
		<input id="remember" type="checkbox" name="remember" />
		<label  for="remember">记住登录状态</label>
		</span>
	</div>
</template>
 
<script>
	import {mapState,mapActions} from 'vuex'
// 导入组件 
export default {
	name: 'loginlogin',
	data() {
            return {
                user_tel: '',
                errFlag: false,
				password: ''
            }
        },
	methods: {
            login: function() {
                this.$http.jsonp(this.web_path+'scoupon/gg/passwdLogin.do?user_tel='+this.user_tel+'&password='+this.password)
                .then(function(res) {
					var flag = res.body.flag;
					if(flag=='true' || flag==true) {
						window.location.href = '#/app1';
					} else {
						this.errFlag = true;
					}
                })
            },
        },
	computed:
		mapState(['web_path'])
}
</script>
<style lang="less" src="../style/login.less"></style>




