import * as types from './mutation-types'
import toStorage from '../lib/storage'
import cookies from '../lib/cookie'
import Filter from '../lib/filter'

export const getUserInfo = ({ commit },res) => {
        toStorage('user_id', res.user_id);
        cookies('user_id', res.user_id);
        toStorage('user_pic', res.user_pic);
        toStorage('user_tel', res.user_tel);
        toStorage('user_nick', res.user_nick);
        toStorage('real_name', res.real_name);
        toStorage('alipay_user_name', res.alipay_user_name);
        toStorage('balance', res.balance);
        res.create_time = Filter.formatCreateTime(res.create_time);
        toStorage('create_time', res.create_time);
        toStorage('unused', res.unused);
        commit(types.GET_USER_INFO, {
            userInfo: res
        })
}
export const changeUsernick = ({ commit }, user_nick) => {
	toStorage('user_nick', user_nick);
    commit('setUsernick', {
        user_nick: user_nick
    })
}
export const changeReal_name = ({ commit }, real_name) => {
	toStorage('real_name', real_name);
    commit('setReal_name', {
        real_name: real_name
    })
}
export const changeAlipay_user_name = ({ commit }, alipay_user_name) => {
	toStorage('alipay_user_name', alipay_user_name);
    commit('setAlipay_user_name', {
        alipay_user_name: alipay_user_name
    })
}
export const changeIndex = ({ commit }, navIndex) => {
    commit(types.CHANGE_NAV_INDEX, {
        navIndex: navIndex
    })
}
export const changeLoginFlag = ({ commit }, login_flag) => {
    commit(types.CHANGE_LOGIN_FLAG, {
        login_flag: login_flag
    })
}


