<template>
	<div id="app">
		<el-container>
			<el-header>
				<div class="logo"></div>
				<div class="drop-menu">
				<el-dropdown>
				<span class="el-dropdown-link">
				<i class="el-pic"><img :src="user_pic" alt=""></i>{{user_nick}}<i class="el-icon-arrow-down el-icon--right"></i>
				</span>
				<el-dropdown-menu slot="dropdown">
				<el-dropdown-item><router-link to="/app1/userCenter">个人中心</router-link></el-dropdown-item>
				<el-dropdown-item divided><a href="#" v-on:click="logout">退出</a></el-dropdown-item>
				</el-dropdown-menu>
				</el-dropdown>
				</div>
			</el-header>
			<el-container>
				<el-aside width="200px">
					<ul class="aside-ul">
						<li :class="[navIndex == 0 ? 'active' : '']"><router-link to="/app1/orderDetail"><img src="./assets/order_detail.png" alt="">订单明细</router-link></li>
						<li :class="[navIndex == 1 ? 'active' : '']"><router-link to="/app1/drawMoney"><img src="./assets/draw_money.png" alt="">提购物金</router-link></li>
						<li :class="[navIndex == 2 ? 'active' : '']"><router-link to="/app1/drawDetail"><img src="./assets/draw_detail.png" alt="">提现明细</router-link></li>
						<li :class="[navIndex == 3 ? 'active' : '']"><router-link to="/app1/returnDetail"><img src="./assets/redraw_detail.png" alt="">返现明细</router-link></li>
						<li><a target="_blank" href="#"><img src="./assets/ques.png" alt="">常见问题</a></li>
						<li><span class="span"><img src="./assets/qq.png" alt="">官方QQ群</span><span class="span2">暂无</span></li>
					</ul>
				</el-aside>
				<el-container>
					<el-main><router-view></router-view></el-main>
					<el-footer>团队 版权所有 </el-footer>
				</el-container>
			</el-container>
		</el-container>
	</div>
</template>

<script>
import { mapActions } from 'vuex'
import { mapGetters } from 'vuex'

export default {
  name: 'app1',
  created: function() {
    this.getUser();
  },
  computed: {
    ...mapGetters([
      'user_nick',
      'login_flag',
      'web_path',
      'user_pic',
	  	'navIndex'
    ])
  },
  mounted: function() {
    this.$nextTick(function() {
      window.onresize = () => {
          // location.reload();
      }
    })
  },
  methods: {
    ...mapActions([
        'getUserInfo'
    ]),
	logout:function(){
		this.$http.jsonp(this.web_path+'scoupon/user/logout.do')
		.then(function(res) {
			var flag = res.body.flag;
			if(flag=='true' || flag==true) {
				window.location.href = '#/login';
			}
		})
	},
	getUser:function(){
		this.$http.jsonp(this.web_path+'scoupon/gg/selectUser.do')
		.then(function(res) {
			this.getUserInfo(res.body);
			this.flag = this.login_flag;
			if(this.login_flag){
				window.location.href = '#/app1';
			} else {
				window.location.href = '#/login';
			}			
		})
	}	
  }
}
</script>

<style lang="less" src="./style/App.less"></style>







