import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'
import * as types from './mutation-types'

Vue.use(Vuex)

const state = {
    user_nick: '',
    //web_path: 'http://127.0.0.1:8080/',
    web_path: 'http://xiaoshuili.xyz:8080/',
    login_flag: false,
    user_tel: '',
    real_name: '',
    alipay_user_name: '',
    balance: '',
    create_time: '',
    unused: '',
    user_pic: '',
    navIndex: '',
    user_id: ''
}

const mutations = {
    [types.GET_USER_INFO] (state, payload) {
        state.user_tel = payload.userInfo.user_tel;
        state.user_nick = payload.userInfo.user_nick;
        state.real_name = payload.userInfo.real_name;
        state.alipay_user_name = payload.userInfo.alipay_user_name;
        state.balance = payload.userInfo.balance;
        state.create_time = payload.userInfo.create_time;
        state.unused = payload.userInfo.unused;
        state.user_pic = payload.userInfo.user_pic;
        state.user_id = payload.userInfo.user_id;
		if(payload.userInfo.login_flag==true || (payload.userInfo.user_id != null && payload.userInfo.user_id != "")){
			state.login_flag = true;
		}else{
			state.login_flag = false;
		}
    },
    [types.CHANGE_NAV_INDEX] (state, payload) {
        state.navIndex = payload.navIndex;
    },
	[types.CHANGE_LOGIN_FLAG] (state, payload) {
        state.login_flag = payload.login_flag;
    },
	setUsernick (state, payload) {
        state.user_nick = payload.user_nick;
    },
	setAlipay_user_name (state, payload) {
        state.alipay_user_name = payload.alipay_user_name;
    },
	setReal_name (state, payload) {
        state.real_name = payload.real_name;
    }
}

export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
})


